import { ErrorHandler, Injectable, Injector } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { NGXLogger } from 'ngx-logger';

@Injectable({
  providedIn: 'root'
})
export class SenecaAngularErrorHandlerService  implements ErrorHandler {

  constructor(private injector: Injector) {}

  handleError(error: Error | HttpErrorResponse): void {
    const logger = this.injector.get(NGXLogger);

    if (error instanceof HttpErrorResponse) {
      logger.error(
        error.message,
        `SenecaErrorHandler: serverErrorId: ${error.error.id}`,
        `SenecaErrorHandler: serverErrorMessage: ${error.error.message}`
      );
    } else {
      logger.error("SenecaErrorHandler: " + error.message, "SenecaErrorHandler: " + error.stack);
    }
  }
}
